
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    scroll-behavior: smooth;
}

li {
    list-style: none;
}

a {
    text-decoration: none; 
}

p {
    letter-spacing: 1px;
    line-height: 30px;
}